*,
*::before,
*::after {
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  margin: 0px;
  padding: 0px;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif;
  overflow: hidden;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}
.tab-bar-root {
  display: flex;
  flex-flow: column;
  height: 100%;
}
.tab-bar-header {
  flex: 0 1 auto;
}
.tab-bar-content {
  flex: 1 1 auto;
  overflow: hidden;
}
body {
  display: flex;
  overflow-y: auto;
  overscroll-behavior-y: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: scrollbar;
}
#jsx-root {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.app-section {
  flex: 1 1 auto;
  overflow: hidden;
}
.menubar {
  flex: 0 1 auto;
  position: static !important;
  width: 100vw !important;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* patch for mui-org/material-ui#18200: clicking on group headers in select causes crash */
.MuiListSubheader-root { pointer-events: none; }

.custom-label {
  color: black !important;
  margin-top: 10px !important;
  font-size: 16px !important;
  font-weight: 600 !important;
  /* font-style: italic !important; */
}
.MuiDialogTitle-root { padding-bottom: 4px !important; }
.MuiButton-containedPrimary {
  background-color: #1976d2 !important;
}
.MuiButton-containedPrimary:hover {
  background-color: #1466b8 !important;
}
#zettel-buttons-container .MuiButton-root {
  padding: 7px 8px;
}
.textcomplete-dropdown {
  border: 1px solid #DDD;
  background-color: white;
  list-style: none;
  padding: 0;
  margin: 0;
  z-index: 99999 !important; /* appear over MUI dialog */
}
.textcomplete-item {
  margin: 0;
  font-size: 14px;
  padding: 2px 5px;
  cursor: pointer;
}
.textcomplete-item:hover,
.textcomplete-item.active {
  background-color: rgba(0, 0, 0, 0.05);
}
.markdown p:first-child {
  margin-top: 0;
}
.markdown p:last-child {
  margin-bottom: 0;
}
.markdown input[type="checkbox"] {
  margin: 0;
  margin-left: -4px;
}
.loading-screen-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading-screen-container {
  flex: 0 0 100px;
  font-size: 2em;
  color: darkgrey;
  font-family: sans-serif;
}
/* Emulate styling of MUI's <TextField multiline /> */
.custom-textarea {
  width: 100%;
  border: 1px solid #c4c4c4;
  resize: none;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;
  transition: border-color 0.1s;
  padding: 10.5px 14px;
  font-family: inherit;
  font-size: inherit;
}
.custom-textarea:focus {
  border: 2px solid #3f51b5;
  padding: 9.5px 13px;
}
/* Textareas resizing to fit contents */
.autoresize-div {
  display: none;
}
textarea.autoresize, .autoresize-div {
  width: 100%;
  min-height: 100px;
  max-height: 100%;
  font-family: Arial, sans-serif;
  font-size: 16px;
  overflow: hidden;
  line-height: 1.4;
  resize: none;
  padding: 10.5px 14px;
  overflow-y: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
}
